import React from 'react';
import './css/HomeBody.css';
import Linkedin from './img/in.svg';
import Github from './img/git.svg';
import FLOCK4H from './img/flock4h.svg';

let about_me = `
22-years-old Cybersecurity student at WSB Merito in Wrocaw, Poland.
I'm an experienced Software, Firmware, and Microcontrollers developer, an Ethical Hacker, and former AI researcher.
My primary interests lie in reverse engineering, physical hacking, radio frequencies, and programming as a whole.
Recently I developed a few projects related to cryptomarket, blockchain, and cryptocurrencies.

Over the years, I've published many successful projects, got posted by Black Hat Ethical Hacking, and Bishop Fox.
Dedicating myself to the projects I work on is always my main priority.
I'm currently seeking employment opportunities in the IT industry and would greatly appreciate any advice or encouragement.
`;

about_me = about_me.trim().split('\n\n').map((line, index) => (
    <p key={index}>{line}</p>
));

const HomePage = () => {
 return (
    <div className="home-body-rect">
        <div className="about-text">
            About me
        </div>
        <div className="home-content">
            <div className="home-content-text">
                    {about_me}
                     <img src={FLOCK4H} alt="FLOCK4H" width={128}/>
            </div>
            <div className="home-content-img">
                <a href="https://www.linkedin.com/in/patryk-andrzejuk-a304a7305" target="_blank" rel="noreferrer">
                    <img src={Linkedin} alt="Linkedin" />
                </a>
                <a href="https://github.com/FLOCK4H" target="_blank" rel="noreferrer">
                    <img src={Github} alt="Github" />
                </a>

            </div>
        </div>
    </div>
 ); 
}

export default HomePage;