import React, { useState } from 'react';
import './css/Projects.css';
import ArrowLeft from './img/arr_left.svg';
import ArrowRight from './img/arr_right.svg';

const projectData = [
    {
        thumbnail: "./3way_social.svg",
        title: "Freeway",
        text: `802.11 Protocol packet monitor and injection tool, powerful Python tool made with Scapy for cybersecurity researchers. 
        The project was being actively developed for 2 months, by this adventure I learned that patience and hard-work are the keys to success.
        Freeway is incorporating: Packet Monitor, Deauthentication Attack, Beacon Flood Attack, various fuzzing techniques, channel hopper and more..`,
        link: `https://github.com/FLOCK4H/Freeway`
    },
    {
        thumbnail: "./jammy_social.png",
        title: "Jammy",
        text: `Collection of self-made and community tools made for ethical hacking, vulnerability assessment and password cracking. Perform attacks on various 
        protocols such as WiFi, Bluetooth or BLE. The project is continuously in development and will always be updated with new tools and features, making it
        a very valuable tool for cybersecurity researchers, or for those who want to find vulnerabilities in their devices.`,
        link: `https://github.com/FLOCK4H/Jammy`
    },
    {
        thumbnail: "./neoducky_social.png",
        title: "NeoDucky",
        text: `Adafruit NeoKey Trinkey microcontroller, programmed to act as an HID when plugged into any device. In other words, a Rubber Ducky, but 
        much cheaper, easy to make and with user-friendly payload syntax. It can consume over 1Kb of payload, and can be used for various attacks, such as 
        PIN bruteforcing, malware injection or data exfiltration.`,
        link: `https://github.com/FLOCK4H/NeoDucky`
    },
    {
        thumbnail: "./bleach_social.svg",
        title: "Bleach",
        text: `This project is a very portable pentesting device, made with an ESP32 microcontroller, it can perform various attacks on protocols such as:
        WiFi, Bluetooth or BLE. Turn Bleach into a bluetooth mouse or keyboard, perform 802.11 packet flooding attacks and more. This experience taught me
        how to modify and compile the firmware in order to add new functionalities and make the device more powerful.`,
        link: `https://github.com/FLOCK4H/Bleach`
    },
    {
        thumbnail: "./atom_ducky.png",
        title: "Atom Ducky",
        text: `Atom Ducky is a HID device controlled through a web browser.
        Functions similarly to NeoDucky with a big advantage of being operated via web interface.
        It's designed to function as a wirelessly operated Rubber Ducky, personal authenticator, or casual keyboard. 
        Its primary aim is to help ethical hackers gain knowledge about Rubber Ducky devices while integrating their use into everyday life.`,
        link: `https://github.com/FLOCK4H/AtomDucky`
    }
];

const Projects = () => {
    const [currentProject, setCurrentProject] = useState(0);

    const handlePrevClick = () => {
        setCurrentProject(prev => (prev - 1 + projectData.length) % projectData.length);
    };

    const handleNextClick = () => {
        setCurrentProject(prev => (prev + 1) % projectData.length);
    };

    return (
        <div className="projects-rect">
            <div className="arrow-left" onClick={handlePrevClick}>
                <img src={ArrowLeft} alt="Arrow Left" width="32"/>
            </div>
            <div className="arrow-right" onClick={handleNextClick}>
                <img src={ArrowRight} alt="Arrow Right" width="32" />
            </div>
            <div className="thumbnail">
                <a href={projectData[currentProject].link} target="_blank" rel="noreferrer">
                    <img src={projectData[currentProject].thumbnail} alt="Project Thumbnail" />
                </a>
            </div>
            <div className="proj-desc">
                <div id="proj-title">
                    {projectData[currentProject].title}
                </div>
                <div id="proj-text">
                    {projectData[currentProject].text}
                </div>
            </div>
        </div>
    );
}

export default Projects;